<template>
  <div id="add-blog"> 
      <h2>Add a New Blog</h2>
      <form>
          <lable>Title:</lable>
          <el-input type="text" v-model.lazy="blog.title" required id="elinput"></el-input>
          <lable>Contents:</lable>
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容" v-model.lazy="blog.content"></el-input>

          <div id="checkboxes">
                <label>Daily</label>
                <input type="checkbox" value="daily" v-model="blog.categories"/>
                <label>Travel</label>
                <input type="checkbox" value="travel" v-model="blog.categories"/>
          </div>

           
        <el-button type="success" v-on:click.prevent="post" @click="open" plain>Add Blog</el-button>

        <hr>

  

      </form>

       <img :src="imageUrl" height="150" v-if="imageUrl" />
            <input type="file" ref="image" accept="image/*" @change="onFilePicked"/>
            <el-button @click="upload">Upload</el-button>

<br>
            <div class="block">
    <span class="demonstration">Date Time</span>
    <el-date-picker
      v-model="blog.currentDataTime"
      type="datetime"
      placeholder="pick the datetime">
    </el-date-picker>
        </div>
            
<hr>


  <div id="preview">
      <h3>Preview Blog</h3>
      <p>Blog title: {{ blog.title }}</p>
      <p>Blog content:</p>
      <p>{{ blog.content }}</p>
      <p>Blog Categories</p>
      <ul>
          <li v-for="category in blog.categories" :key="category.id">{{category}}</li>
      </ul>
  </div>
  </div>
</template>

<script>
import  {db}  from "../main.js";


export default {

  data(){
    return{
        blog:{
         title: "",
         content: "",
         imageUrlBlog: "",
         currentDataTime: "",
         categories: [],
                 },

     photo: null,
      photo_url: null,
      dialog: false,
      imageName: "",
      imageUrl: "",
      imageFile: "",
      
      imgUrls: []
   
    }
},
created(){

 this.getImages();
},
methods: {
    post: function(){
        this.$http.post(
    'https://alanwebsite-484a3.firebaseio.com/posts.json',
        this.blog )
        

        this.blog.title = "";
        this.blog.content = "";
        this.blog.imageUrlBlog = "";

    

    },
    open(){
        this.$alert('You have uploaded a new post!', 'Success!', {
            confirmButtonText: 'OK',
    
        });
    },
     getImages: function() {
      db.collection("images")
        .get()
        .then(snap => {
          const array = [];
          snap.forEach(doc => {
            array.push(doc.data());
          });
          this.imgUrls = array;
        });

      this.imageName = "";
      this.imageFile = "";
      this.imageUrl = "";
    },
    onFilePicked(e) {
      const files = e.target.files;
      if (files[0] !== undefined) {
        this.imageName = files[0].name;
        if (this.imageName.lastIndexOf(".") <= 0) {
          return;
        }
        const fr = new FileReader();
        fr.readAsDataURL(files[0]);
        fr.addEventListener("load", () => {
          this.imageUrl = fr.result;
          //console.log("imageUrl");
          this.imageFile = files[0]; // this is an image file that can be sent to server...
          //this.getImages();
        });
      } else {
        this.imageName = "";
        this.imageFile = "";
        this.imageUrl = "";
      }
    },
    upload: function() {

        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
        }, 2000);
     
      var storageRef = this.$firebase.storage().ref();

      var mountainsRef = storageRef.child(`images/${this.imageName}`);
   
      mountainsRef.put(this.imageFile).then(snapshot => {
        snapshot.ref.getDownloadURL().then(downloadURL => {
          this.imageUrl = downloadURL;
          const bucketName = "alanwebsite-484a3.appspot.com";
          const filePath = this.imageName;
          db.collection("images").add({
            downloadURL,
            downloadUrl:
              `https://firebasestorage.googleapis.com/v0/b/${bucketName}/o/images` +
              "%2F" +
              `${encodeURIComponent(filePath)}?alt=media`,
            timestamp: Date.now()
          });
          console.log(downloadURL);
         this.blog.imageUrlBlog = downloadURL;
         this.blog.content = this.blog.content + 
         "<img src="+downloadURL+ "height=" + 60 +"> ";
          this.getImages();
        });
      });
      //this.getImages();
    }
  },
}

</script>


