<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
       
        <div contenteditable="true"
            v-html="blog.content"
            @input="onDivInput($event)">
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            id: this.$route.params.id,
            blog: {}
        }
    },
    created(){
        this.$http.get('https://alanwebsite-484a3.firebaseio.com/posts/' + this.id + '.json').then(
            function(data){
              return data.json();
                
            }).then(function(data){
                this.blog = data;
            });
    },
    onDivInput: function(e){
        this.blog.content = e.target.innerHTML;
        console
    }
}
</script>

<style>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
}
</style>
